<template>
  <div class="home">
    <Tabs value="1"@on-click="changeTab">
      <TabPane v-for="category of category_list" :key="category.id" :label="category.name" id="category.id">
        <Card v-for="question of question_list" :key="question.question_id" :title="question.caption">
          <p>{{question.content}}</p>
          <p>提问时间：{{question.create_time}} 提问者：{{question.author_name}}</p>
        </Card>
      </TabPane>
    </Tabs>
  </div>
</template>

<script>
export default {
  name: "home",
  data() {
    return {
      question_list: [],
      category_list : [],
    };
  },
  created() {
    this.fetchCategoryList()
  },
  methods: {
    fetchCategoryList() {
      let vm = this
      this.$http.get("/api/category/list").then(function(response){
        if (response.status != 200){
          vm.$Message.error("获取分类失败, 网络错误");
          return;
        }
        if (response.data.code == 0) {
          vm.category_list = response.data.data;
        } else {
          vm.$Message.error(response.data.message);
        }
        if (vm.category_list.length > 0) {
          vm.fetchQuestionList(vm.category_list[0].id);//上边已经定义vm = this, 所以要用vm, 否则会报错
        }
      },function(response){
        vm.$Message.error(response.data.message);
      })

    },
    fetchQuestionList(id) {
      console.info(id);
      let vm = this
      this.$http.get("/api/question/list?category_id="+id).then(function(response){
        if (response.status != 200){
          vm.$Message.error("获取问题列表失败, 网络错误");
          return;
        }
        if (response.data.code == 0) {
          vm.question_list = response.data.data;
        } else {
          vm.$Message.error(response.data.message);
        }
      },function(){
        vm.$Message.error("服务繁忙, 请稍后重试");
      })
    },
    changeTab:function(key){
      const index = parseInt(key);
      this.fetchQuestionList(this.category_list[index].id)
    }
  }
};
</script>
